import React, { useState, useEffect } from 'react';
import authSvg from '../assets/update.svg';
import { ToastContainer, toast } from 'react-toastify';
import axios from 'axios';
import { updateUser, isAuth, getCookie, signout, updateUserData, updateUserImageData, LocalUserData,updateBuktiPoster} from '../helpers/auth';
import Compressor from 'compressorjs';
const UploadBuktiPoster = () => {
  const [formData, setFormData] = useState({
    bukti_pembayaran_poster: '',

    textChange: 'Update'
   
  });
  const [newUser, setNewUser] = useState(
    {     photo: '',
    }
    
);




const handleSubmits = (e) => {
  e.preventDefault();
  toast.info("Proses upload membutuhkan waktu. Tunggu hingga notifikasi terunggah muncul ")
  const formData = new FormData();
  formData.append('file', newUser.photo);//jangan LUPA TAMBAHAIN BEARER FOR TOKEN AUHTORIZ
  axios.post(`${process.env.REACT_APP_API_URL}/upload`, formData)
       .then(res => {
        let sementara = LocalUserData()
        console.log(sementara)
          //ingin disimpan di localdata
          updateBuktiPoster(res, () => {
            toast.success('Bukti poster terupload. Jangan lupa simpan perubahan');
          });
       })
       .catch(err => {
          console.log(err);
        });
      }



const handlePhoto = (e) => {
  setNewUser({...newUser, photo: e.target.files[0]});
}

const handleDeleteF1 = (e) => {
    
  let URL = ''
  const {bukti_pembayaran_poster  } = LocalUserData();
  URL = bukti_pembayaran_poster;

  
  axios.post(`${process.env.REACT_APP_API_URL}/deleteImage`, { 
    URL
    })
       .then(res => {
        let sementara = LocalUserData()
        console.log(sementara)
        console.log("TEST")
         //ingin disimpan di localdata
         updateBuktiPoster(res, () => {
           toast.success('Bukti pembayaran poster dihapus');
         });
         
          
       })
       .catch(err => {
          console.log(err);
       });
}
  
const handleSubmit = e => {

  const token = getCookie('token');
  console.log(token);

  e.preventDefault();
const {bukti_pembayaran_poster} = LocalUserData()
  setFormData({ ...formData, textChange: 'Submitting' });
  axios
    .put(
      `${process.env.REACT_APP_API_URL}/userData/updatepembayaran`,
      {

        bukti_pembayaran_poster
      },
      {
        headers: {
          Authorization: `Bearer ${token}`//apakah berhubungan dengan JWT ? yea ternyata berhubungan pada JWT_SECRET require Sign-in
        }
      }
    )
    .then(res => {
      updateUserData(res, () => {
        toast.success('Bukti pembayaran poster telah tersimpan');
        setFormData({ ...formData, textChange: 'Update' });
      });
    })
    .catch(err => {
      console.log(err.response);
    });
};


return (
  
        <div className='mt-12 flex flex-col items-center'>
          <ToastContainer />
          <h1 className='text-2xl xl:text-3xl font-extrabold'>
          Upload Bukti Pembayaran Lomba Poster
          </h1>
          <form
            className='w-full flex-1 mt-8 text-indigo-500'
            onSubmit={handleSubmits}
          >
            <div className='mx-auto max-w-xs relative '>
                <span className = "font-medium ">
                   Submisi Poster
                </span>

            <input 
              type="file" 
              accept=".png, .jpg, .jpeg, .pdf"
              name="photo"
              onChange={handlePhoto}
            />
              <button
                type='submit'
                className='mt-5 tracking-wide font-semibold bg-indigo-500 text-gray-100 w-full py-4 rounded-lg hover:bg-indigo-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none'
              >
    
                <span className='ml-3'>Upload Bukti</span>
              </button>
            </div>
           
            
          </form>

          <button className='mt-5 tracking-wide font-semibold bg-pink-500 text-gray-100 w-full py-4 rounded-lg hover:bg-pink-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none'
               onClick={() => handleDeleteF1()}>
               <span className='ml-3'>Delete</span>
              </button> 
         
          <form
            className='w-full flex-1 mt-8 text-indigo-500'
            onSubmit={handleSubmit}
          >
            <div className='mx-auto max-w-xs relative '>
                <span className = "font-medium ">
                   
                </span>
              
              <button
                type='submit'
                className='mt-5 tracking-wide font-semibold bg-indigo-500 text-gray-100 w-full py-4 rounded-lg hover:bg-indigo-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none'
              >
                <i className='fas fa-save fa 1x w-6  -ml-2' />
                <span className='ml-3'>Simpan Data</span>
              </button>
            </div>
            <div className='my-12 border-b text-center'>
              <div className='leading-none px-2 inline-block text-sm text-gray-600 tracking-wide font-medium bg-white transform translate-y-1/2'>
                Go To Home
              </div>
            </div>
            <div className='flex flex-col items-center'>
              <a
                className='w-full max-w-xs font-bold shadow-sm rounded-lg py-3
         bg-indigo-100 text-gray-800 flex items-center justify-center transition-all duration-300 ease-in-out focus:outline-none hover:shadow focus:shadow-sm focus:shadow-outline mt-5'
                href='/'
                target='_self'
              >
                <i className='fas fa-sign-in-alt fa 1x w-6  -ml-2 text-indigo-500' />
                <span className='ml-4'>Home</span>
              </a>
            </div>
          </form>
        </div>
      
);
};

export default UploadBuktiPoster;
