

import { ToastContainer, toast } from 'react-toastify';
import axios from 'axios';
import { updateUser, isAuth, getCookie, signout, updateUserData, updateUserImageData, LocalUserData,updateUserImageKTM1,updateUserImageKTM2} from '../helpers/auth';
import React, { useState, useEffect, useContext } from 'react';
import { useHistory } from 'react-router-dom';

const LombaAdmin = () => 
{
    const history = useHistory();
  useEffect(() => {
        getdata()
    }, [])

    const [getuserdata, setUserdata] = useState([]);
    const getdata = async  () => {
        const token = getCookie('token');
        const res = await fetch(`${process.env.REACT_APP_API_URL}/getposterdata`, {
            method: "GET",
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`,
            }
            
        });

        const data = await res.json();
        console.log(data);

        if (res.status === 422 || !data) {
            console.log("error ");

        } else {
            setUserdata(data)
            console.log("get data");

        }
       
        
        
    }
    const handleClick = async (id) => {
      const token = getCookie('token');
      const data = { lomba: 'Poster' };
      const res = await fetch(`${process.env.REACT_APP_API_URL}/userData/admin/${id}`, {
        method: "PUT",
        headers: {
            "content-type": "application/json",
            'Authorization': `Bearer ${token}`,
        },
        body : JSON.stringify(data)        
        
    });
    getdata();
      toast.success('Tim terverifikasi');
     

    }
    const handleClick2 = (id) => {
        history.push(`/profil/${id}`);

        
  
      }
    

    const Terverifikasi = (response) => {
     {
          const Poster =  response
          if (Poster === "Terverifikasi") {
              return true;
      }else {
          return false;
      }//Kode ini harusnta 
  };
  }
  
return (


    <>
    <div className='min-h-screen bg-gray-100 text-gray-900 flex justify-center'>
      <ToastContainer />
      <div className='max-w-screen-xl m-0 sm:m-20 bg-white shadow sm:rounded-lg flex flex-1'>
        <div className='lg:w-1/2 xl:w-5/12 p-6 sm:p-12'>
          <div className='mt-12 flex flex-col items-center'></div>
            <div className="container">

                <table className="table-auto">
                    <thead>
                        <tr className="bg-gray-50">
                            <th className="px-6 py-2 text-xs text-gray-500" scope="col">id</th>
                            <th className="px-6 py-2 text-xs text-gray-500" scope="col">Username</th>
                            <th className="px-6 py-2 text-xs text-gray-500" scope="col">email</th>
                            <th className="px-6 py-2 text-xs text-gray-500" scope="col">Nama Tim</th>
                            <th   className="px-6 py-2 text-xs text-gray-500" scope="col">Data Tim Terupdate</th>
                             <th  className="px-6 py-2 text-xs text-gray-500" scope="col">Status Pembayaran</th>


                            <th className="px-6 py-2 text-xs text-gray-500" scope="col"></th>
                        </tr>
                    </thead>
                    <tbody class="bg-white" >
            
                        {
                            getuserdata.map((element, id) => {
                                return (
                                    <>
                                        <tr className="whitespace-nowrap">
                                            <th scope="row">{id + 1}</th>
                                            <td className="px-6 py-4 text-sm text-black-500">{element.user_id}</td>
                                            <td className="px-6 py-4 text-sm text-black-500">{element.email}</td>
                                            <td className="px-6 py-4 text-sm text-black-500">{element.nama_tim}</td>
                                            <td className="px-6 py-4 text-sm text-black-500" >{element.terupdate}</td>
                                            <td className="px-6 py-4 text-sm text-black-500" >{element.lomba_poster}</td>
                                            <td className="d-flex justify-content-between">
                                            
                                            <button className='mt-5 tracking-wide font-semibold bg-pink-500 text-gray-100 w-full py-4 rounded-lg hover:bg-pink-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none'
                                            onClick={() => handleClick2(element.user_id)}>
                                              <span className='ml-3'>Profil</span>
                                              </button>                         
                </td>
                                            
                                            <td className="d-flex justify-content-between"></td>
                                            <td className="d-flex justify-content-between">
                                            {  !Terverifikasi(element.lomba_poster) ?
                                            <button className='mt-5 tracking-wide font-semibold bg-pink-500 text-gray-100 w-full py-4 rounded-lg hover:bg-pink-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none'
                                            onClick={() => handleClick(element.user_id)}>
                                              <span className='ml-3'>Verifikasi</span>
                                              </button> :null }
                
                  
               
                                            
                </td>
                                        </tr>
                                    </>
                                )
                            })
                        }
                    </tbody>
                </table>


            </div>
        </div>
        </div>
        </div>
    </>
)
}
export default LombaAdmin;