import React, { useState, useEffect } from 'react';
import authSvg from '../assets/update.svg';
import { ToastContainer, toast } from 'react-toastify';
import axios from 'axios';
import { updateUser, isAuth, getCookie, signout, updateUserData } from '../helpers/auth';

const ProfileFix= ({ history }) => {
  const [formData, setFormData] = useState({
    email: '',
    nama_tim: '',
    institusi: '',
    link_ktm_ketua :'',
    link_ktm_anggota_1:'',
    link_ktm_anggota_2:'',
    nama_ketua : '',
    nama_anggota_1 : '',
    nama_anggota_2 : '',
    nomor_telepon_ketua: '',
    nomor_telepon_cadangan: ''

  });

  useEffect(() => {
    loadProfile();
  }, []);

  const loadProfile = () => {//Dikode ini juga dapat ditambahkan link untuk ktm
  
    const token = getCookie('token');//mengambil token yang disimpan di dalam cookie
    axios
      .get(`${process.env.REACT_APP_API_URL}/userData/${isAuth()._id}`, {
        headers: { // masih bingung gunanya headers ? /// parameternya satu aja kah ini?
          Authorization: `Bearer ${token}`
        }
      })
      .then(res => {
        const { institusi, nama_tim, email, nama_ketua, nama_anggota_1, nama_anggota_2,
          nomor_telepon_ketua,
          nomor_telepon_cadangan,link_ktm_anggota_1, link_ktm_anggota_2,link_ktm_ketua, link_foto_ketua, link_foto_anggota_1, link_foto_anggota_2 } = res.data;
        setFormData({ ...formData, institusi, nama_tim, email, nama_ketua, nama_anggota_1, nama_anggota_2,link_ktm_anggota_1, link_ktm_anggota_2,link_ktm_ketua,
          nomor_telepon_ketua,
          nomor_telepon_cadangan, link_foto_ketua, link_foto_anggota_1, link_foto_anggota_2 });
      })
      .catch(err => {
        toast.error(`Eror mengenai informasi anda ${err.response.statusText}`);
        if (err.response.status === 401) {
          signout(() => {
            history.push('/login');
          });
        }
      });

      
  };
  const {  institusi, nama_tim,email, link_ktm_ketua,
    nama_ketua, nama_anggota_1, nama_anggota_2,
    nomor_telepon_ketua,
    nomor_telepon_cadangan,link_ktm_anggota_1, link_ktm_anggota_2, link_foto_ketua, link_foto_anggota_1, link_foto_anggota_2,textChange } = formData;
 
  
  return (
    <div className=''>
      <div className=''>
        <div className=''>
          <div className='mt-12 flex flex-col items-center'>
            <h1 className='text-2xl xl:text-3xl font-extrabold'>
              Informasi Data Tim
            
            </h1>

            <form
              className='w-full flex-1 mt-8 text-indigo-500'
            >
              <div className='mx-auto max-w-xs relative '>
                  <span className = "font-medium ">
                     Nama Tim
                  </span>
                <input
                  className='w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5'
                  type='nama_tim'
                  placeholder='Nama Tim'
                  disabled
                  value={nama_tim}
                />
                <span className = "font-medium ">
                    Institusi
                  </span>
                <input
                  className='w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5'
                  type='text'
                  placeholder='Insitusi'
                  disabled
                  value={institusi}
                />
                <span className = "font-medium ">
                     Nama Ketua
                  </span>
                
                <input
                  className='w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5'
                  type='text'
                  placeholder='Nama Ketua'
                  disabled
                  value={nama_ketua}
                />
                <span className = "font-medium ">
                     Nama Anggota 1
                  </span>
                <input
                  className='w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5'
                  type='nama anggota 1'
                  placeholder='Nama Anggota 1'
                  disabled
                  value={nama_anggota_1}
                />
                <span className = "font-medium ">
                     Nama Anggota 2
                  </span>
                <input
                  className='w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5'
                  type='text'
                  placeholder='Nama Anggota 2'
                  disabled
                  value={nama_anggota_2}
                />
                <span className = "font-medium ">
                     Nomor Telepon Ketua
                  </span>
                <input
                  className='w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5'
                  type='text'
                  placeholder='Nomor Telepon Ketua'
                  disabled
                  value={nomor_telepon_ketua}
                />
                <span className = "font-medium ">
                     Nama Telepon Cadangan
                  </span>
                  <input
                  className='w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5'
                  type='text'
                  placeholder='Nomor Telepon Cadangan'
                  disabled
                  value={nomor_telepon_cadangan}
                />
                
              </div>
              <div className='flex flex-col items-center'>
              <button
                onClick={()=> window.open(link_ktm_ketua, "_blank") }
                className='mt-5 tracking-wide font-semibold bg-indigo-500 text-gray-100 w-full py-4 rounded-lg hover:bg-indigo-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none'
              >
                <span className='ml-3'>Download Link KTM ketua</span>
              </button>
              <button
                onClick={()=> window.open(link_ktm_anggota_1, "_blank") }
                className='mt-5 tracking-wide font-semibold bg-indigo-500 text-gray-100 w-full py-4 rounded-lg hover:bg-indigo-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none'
              >
                <span className='ml-3'>Download Link KTM anggota 1</span>
              </button>
              <button
                onClick={()=> window.open(link_ktm_anggota_2, "_blank") }
                className='mt-5 tracking-wide font-semibold bg-indigo-500 text-gray-100 w-full py-4 rounded-lg hover:bg-indigo-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none'
              >
                <span className='ml-3'>Download Link KTM anggota 2</span>
              </button>
              </div>
              
              <div className='my-12 border-b text-center'>
                <div className='leading-none px-2 inline-block text-sm text-gray-600 tracking-wide font-medium bg-white transform translate-y-1/2'>
                  Go To Home
                </div>
              </div>
              <div className='flex flex-col items-center'>
                <a
                  className='w-full max-w-xs font-bold shadow-sm rounded-lg py-3
           bg-indigo-100 text-gray-800 flex items-center justify-center transition-all duration-300 ease-in-out focus:outline-none hover:shadow focus:shadow-sm focus:shadow-outline mt-5'
                  href='/'
                  target='_self'
                >
                  <i className='fas fa-sign-in-alt fa 1x w-6  -ml-2 text-indigo-500' />
                  <span className='ml-4'>Home</span>
                </a>

              </div>

              <a
                  className='w-full max-w-xs font-bold shadow-sm rounded-lg py-3
           bg-indigo-100 text-gray-800 flex items-center justify-center transition-all duration-300 ease-in-out focus:outline-none hover:shadow focus:shadow-sm focus:shadow-outline mt-5'
                  href='/editprofil'
                  target='_self'
                >
                  <i className='fas fa-sign-in-alt fa 1x w-6  -ml-2 text-indigo-500' />
                  <span className='ml-4'>Edit profil</span>
                </a>
            </form>
          </div>
        </div>
       
      </div>
      ;
    </div>
  );
};

export default ProfileFix;
