import React, { useState } from 'react';
import authSvg from '../assets/auth.svg';
import { ToastContainer, toast } from 'react-toastify';
import axios from 'axios';
import { authenticate, isAuth } from '../helpers/auth';
import { Link, Redirect } from 'react-router-dom';

import loginBackground from '../assets/LoginBackground.png';
import logoNesco from '../assets/LogoNesco.png';


const Register = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password1: '',
    password2: '',
    textChange: 'Sign Up'
  });

  const { name, email, password1, password2, textChange } = formData;
  const handleChange = text => e => {
    setFormData({ ...formData, [text]: e.target.value });
  };
  const handleSubmit = e => {
    e.preventDefault();
    if (name && email && password1) {
      if (password1 === password2) {
        setFormData({ ...formData, textChange: 'Submitting' });
        axios
          .post(`${process.env.REACT_APP_API_URL}/register`, {
            name,
            email,
            password: password1
          })
          .then(res => {
            setFormData({
              ...formData,
              name: '',
              email: '',
              password1: '',
              password2: '',
              textChange: 'Submitted'
            });

            toast.success(res.data.message);
          })
          .catch(err => {
            setFormData({
              ...formData,
              name: '',
              email: '',
              password1: '',
              password2: '',
              textChange: 'Sign Up'
            });
            console.log(err.response);
            toast.error(err.response.data.errors);
          });
      } else {
        toast.error("Password tidak sesuai");
      }
    } else {
      toast.error('Isikan seluruh informasi yang dibutuhkan');
    }
  };

  return (
    <header className='flex w-full' style={{ backgroundImage: "url(" + loginBackground + ")", backgroundSize: 'cover' }}>
      {isAuth() ? <Redirect to='/' /> : null} {/* JIKA ADA COOKIE MAKA REDIRECT KE HOME */}
      <ToastContainer />
      <Link to="/">
      <img alt='' src={logoNesco} className='h-32'></img>
      </Link>
      <Link to="/"><h1 className=' text-white my-8 text-4xl hover:text-nesco-text-purple transition-colors'>NESCO 2022</h1></Link>
      <form onSubmit={handleSubmit} className=' bg-nesco-cyan/30 backdrop-blur-md flex-1 h-1/3 w-2/3 max-w-xl rounded-3xl outline outline-2 outline-nesco-blue/80 p-6' style={{maxHeight:512}}>
      <input className=' bg-transparent w-full under focus:outline-none placeholder:text-white text-white' placeholder="Nama" onChange={handleChange('name')} value={name}></input>
        <hr></hr>
        <div className=' h-7'></div>
        <input className=' bg-transparent w-full under focus:outline-none placeholder:text-white text-white' placeholder="Email" onChange={handleChange('email')} value={email} ></input>
        <hr></hr>
        <div className=' h-7'></div>
        <input type={'password'} className=' bg-transparent w-full under focus:outline-none placeholder:text-white text-white' placeholder="Password" onChange={handleChange('password1')} value={password1}></input>
        <hr></hr>
        <div className=' h-7'></div>
        <input type={'password'} className=' bg-transparent w-full under focus:outline-none placeholder:text-white text-white' placeholder="Konfirmasi Password" onChange={handleChange('password2')} value={password2}></input>
        <hr></hr>
        <div className=' h-7'></div>
        <br></br>
        <div className=' mx-auto text-center'>
          <button type='submit' className=' my-4 text-center mx-auto px-10 py-2 rounded-full text-white' style={{ background: "linear-gradient(109.93deg, #FFA62E 10.81%, rgba(255, 166, 46, 0.7) 60.13%, rgba(255, 166, 46, 0.5) 92.27%)" }}>
            <h1>REGISTER</h1>
          </button>
          <div className=' block md:flex justify-center'>
            <p className=' text-lg text-white'>Sudah punya akun?</p>
            <div className=' w-2'></div>
            <Link className=' text-lg text-white hover:text-nesco-text-purple transition-colors' to='/login'>Login</Link>
          </div>
        </div>
      </form>
    </header>
    
  );
};

export default Register;
