import React, { useState, useEffect } from 'react';
import authSvg from '../assets/update.svg';
import { ToastContainer, toast } from 'react-toastify';
import axios from 'axios';
import { updateUser, isAuth, getCookie, signout, updateUserImageProfile } from '../helpers/auth';
import Compressor from 'compressorjs';





const PrivateContent = ({ history }) => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password1: '',
    textChange: 'Update',
    institusi:'',
    role: '',

    link_profil: ''
  });
  const [newUser, setNewUser] = useState(
    {
      photo: '',
    })
  useEffect(() => {
    loadProfile();
  }, []);

  const loadProfile = () => {

    const token = getCookie('token');//mengambil token yang disimpan di dalam cookie
    axios
      .get(`${process.env.REACT_APP_API_URL}/user/${isAuth()._id}`, {
        headers: { // masih bingung gunanya headers ? 
          Authorization: `Bearer ${token}`
        }
      })
      .then(res => {
        const { role, name, email, link_profil,institusi } = res.data;
        setFormData({ ...formData, role, name, email,institusi, link_profil });
      })
      .catch(err => {
        toast.error(`Error To Your Information ${err.response.statusText}`);
        if (err.response.status === 401) {
          signout(() => {
            history.push('/login');
          });
        }
      });
  };
  const { name, email, password1, password2, textChange, institusi, link_profil, role } = formData;
  const handleChange = text => e => {
    setFormData({ ...formData, [text]: e.target.value });
  };


  const handleSubmits = (e) => {
    toast.info("Proses upload membutuhkan waktu. Tunggu hingga notifikasi terunggah muncul ")
    e.preventDefault();
    const image = newUser.photo;
    new Compressor(image,
      {
        convertTypes: 'image/png', convertSize: 500000,
        success: (compressedResult) => {
          const formData = new FormData();
          formData.append('file', compressedResult, compressedResult.name);
          axios.post(`${process.env.REACT_APP_API_URL}/upload`, formData)
            .then(res => {


              updateUserImageProfile(res, () => {
                toast.success('Gambar profil terupload. Jangan lupa simpan perubahan.');
              });
            })
            .catch(err => {
              console.log(err);
            });
        },
      });

  }


  const handlePhoto = (e) => {
    setNewUser({ ...newUser, photo: e.target.files[0] });
  }
  const handleDeleteF1 = (e) => {

    let URL = ''
    const { link_profil } = isAuth();
    URL = link_profil;


    axios.post(`${process.env.REACT_APP_API_URL}/deleteImage`, {
      URL
    })
      .then(res => {
        let sementara = isAuth()
        console.log(sementara)
        console.log("TEST")
        //ingin disimpan di localdata
        updateUserImageProfile(res, () => {
          toast.success('Gambar profil berhasil dihapus');
        });


      })
      .catch(err => {
        console.log(err);
      });
  }
  const handleSubmit = e => {
    const token = getCookie('token');
    const { link_profil } = isAuth();

    console.log(isAuth());
    console.log(token);
    e.preventDefault();
    if (password1 === password2) {
      setFormData({ ...formData, textChange: 'Submitting' });
      axios
        .put(
          `${process.env.REACT_APP_API_URL}/user/update`,
          {
            name,
            email,
            institusi,
            
            password: password1,
            link_profil
          },
          {
            headers: {
              Authorization: `Bearer ${token}`//apakah berhubungan dengan JWT ? yea ternyata berhubungan pada JWT_SECRET require Sign-in
            }
          }
        )
        .then(res => {
          updateUser(res, () => {
            window.location.reload();
            loadProfile();
            toast.success('Profil akun berhasil diupdate');
            setFormData({ ...formData, textChange: 'Update' });

            
          });
        })
        .catch(err => {
          setFormData({
            ...formData,

            institusi: '',
            password1: '',
            password2: ''

          });
          toast.error(err.response.data.error);
          console.log(err.response);

        });
    }
    else {
      toast.error("Kedua Password tidak sesuai");
    }
  };

  return (      
            <div className='lg:w-1/2 xl:w-5/12 p-6 sm:p-12'>
              <div className='mt-12 flex flex-col items-center'>
                <h1 className='text-2xl xl:text-3xl font-extrabold'>
                  Profile Update
                </h1>

                <form
                  className='w-full flex-1 mt-8 text-indigo-500'
                  onSubmit={handleSubmits}
                >
                  <div className='mx-auto max-w-xs relative '>
                    <span className="font-medium ">
                      Upload Foto Profil
                    </span>

                    <input
                      type="file"
                      accept=".png, .jpg, .jpeg"
                      name="photo"
                      onChange={handlePhoto}
                    />
                    <button
                      type='submit'
                      className='mt-5 tracking-wide font-semibold bg-indigo-500 text-gray-100 w-full py-4 rounded-lg hover:bg-indigo-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none'
                    >

                      <span className='ml-3'>Upload Gambar</span>
                    </button>
                  </div>


                </form>

                <button className='mt-5 tracking-wide font-semibold bg-pink-500 text-gray-100 w-full py-4 rounded-lg hover:bg-pink-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none'
                  onClick={() => handleDeleteF1()}>
                  <span className='ml-3'>Delete</span>
                </button>
                <form
                  className='w-full flex-1 mt-8 text-indigo-500'
                  onSubmit={handleSubmit}
                >
                  <div className='mx-auto max-w-xs relative '>
                    <input
                      disabled
                      className='w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white'
                      type='text'
                      placeholder='Role'
                      value={role}
                    />
                    <input
                      className='w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5'
                      type='email'
                      placeholder='Email'
                      disabled
                      value={email}
                    />
                    <input
                      className='w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5'
                      type='text'
                      placeholder='Name'
                      onChange={handleChange('name')}
                      value={name}
                    />
                    <input
                      className='w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5'
                      type='text'
                      placeholder='Institusi'
                      onChange={handleChange('institusi')}
                      value={institusi}
                    />

                    <input
                      className='w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5'
                      type='password'
                      placeholder='Password'
                      onChange={handleChange('password1')}
                      value={password1}
                    />
                    <input
                      className='w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5'
                      type='password'
                      placeholder='Password'
                      onChange={handleChange('password2')}
                      value={password2}
                    />
                    <button
                      type='submit'
                      className='mt-5 tracking-wide font-semibold bg-indigo-500 text-gray-100 w-full py-4 rounded-lg hover:bg-indigo-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none'
                    >
                      <i className='fas fa-user-plus fa 1x w-6  -ml-2' />
                      <span className='ml-3'>{textChange}</span>
                    </button>
                  </div>
                  <div className='my-12 border-b text-center'>
                    <div className='leading-none px-2 inline-block text-sm text-gray-600 tracking-wide font-medium bg-white transform translate-y-1/2'>
                      Go To Home
                    </div>
                  </div>
                  <div className='flex flex-col items-center'>
                    <a
                      className='w-full max-w-xs font-bold shadow-sm rounded-lg py-3 bg-indigo-100 text-gray-800 flex items-center justify-center transition-all duration-300 ease-in-out focus:outline-none hover:shadow focus:shadow-sm focus:shadow-outline mt-5'
                      href='/debug'
                      target='_self'
                    >
                      <i className='fas fa-sign-in-alt fa 1x w-6  -ml-2 text-indigo-500' />
                      <span className='ml-4'>Home</span>
                    </a>
                  </div>
                </form>
              </div>
            </div>
  )
          
};

export default PrivateContent;
