import { Link } from 'react-router-dom';
import Logo from '../assets/LogoNescoRounded.png';
import React, { useRef, useEffect, useLayoutEffect, useCallback } from 'react';
import { authenticate, isAuth, isAdmin, LocalUserData } from '../helpers/auth';
import { Menu, Transition } from '@headlessui/react';

export default function Navbar() {

  const [isSticky, setIsSticky] = React.useState(true);
  const [isOpened, setIsOpened] = React.useState(false);

  const onScroll = () => {
    if (window.scrollY > 30) {
      setIsSticky(true);
    } else {
      setIsSticky(false);
    }
  }


  useEffect(() => {
    window.addEventListener('scroll', onScroll);

    return () => {
      window.removeEventListener('scroll', onScroll);
    }
  });


  return (
    <div className={`fixed top-0  inline-block  w-full items-stretch`}>
      <nav className={` top-0 w-full ju transition z-10 text-white  ${isSticky ? "bg-gradient-to-r from-nesco-bg-purple to-nesco-bg-blue shadow-2xl " : " absolute top-0 bg-transparent"} `}>
        <div className=" mx-auto w-full px-6 py-2 flex justify-between items-center">
          <div className=" inline-flex align-middle h-16 items-center">
            <Link to="/">
              <img alt='' src={Logo} className=' h-12'></img></Link>
            <Link to="/"><h1 className=" m-4 text-base lg:text-4xl  font-extrabold text-white hover:text-blue-200">NESCO 2022</h1></Link>


          </div>
          <ul className=' hidden md:inline-flex justify-around list-none'>
            <li><Link to="/" className='px-4 hover:text-blue-200'>FAQ</Link></li>
            <li>
              <Menu>
                <Menu.Button className='px-4 hover:text-blue-200'>Competition</Menu.Button>
                <Transition
                  enter="transition ease-out duration-100"
                  enterFrom="transform opacity-0 scale-95"
                  enterTo="transform opacity-100 scale-100"
                  leave="transition ease-in duration-75"
                  leaveFrom="transform opacity-100 scale-100"
                  leaveTo="transform opacity-0 scale-95"
                  className='relative'
                >
                  <Menu.Items as="ul" className='text-nesco-text-purple bg-white absolute p-2 m-2 rounded-lg'>
                    <Menu.Item as="li">
                      {({ active }) => (
                        <Link to="/competitions/paper">
                          <a
                            className={`${active && "bg-nesco-bg-purple text-white"
                              } flex rounded-md text-center w-full p-2 text-xs`}
                          >
                            Paper Competition
                          </a>
                        </Link>
                      )}
                    </Menu.Item>
                    <Menu.Item as="li">
                      {({ active }) => (
                        <Link to="/competitions/poster">
                          <a
                            className={`${active && "bg-nesco-bg-purple text-white"
                              } flex rounded-md text-center w-full p-2 text-xs`}
                          >
                            Poster Competition
                          </a>
                        </Link>
                      )}
                    </Menu.Item>
                    <Menu.Item as="li">
                      {({ active }) => (
                        <Link to="/competitions/ei">
                          <a
                            className={`${active && "bg-nesco-bg-purple text-white"
                              } flex rounded-md text-center w-full p-2 text-xs`}
                          >
                            Electrical Innovation
                          </a>
                        </Link>
                      )}
                    </Menu.Item>
                  </Menu.Items>
                </Transition>
              </Menu></li>
            <li><Link to="/" className='px-4 hover:text-blue-200'>Webinar</Link></li>
            <li><Link to="/" className='px-4 hover:text-blue-200'>Gallery</Link></li>
            <li>{!isAuth() ? <Link to="/login" className='px-4 hover:text-blue-200'>Sign In</Link> :null} </li>  
            <li>{isAuth() ?
              <Link to="/debug" className='px-4 hover:text-blue-200'>Debug</Link> : null}</li>
          </ul>
          <div className=' md:hidden' onClick={() => setIsOpened(!isOpened)}>
            <svg
              className="fill-current h-3 w-3"
              viewBox="0 0 20 20"
              xmlns="http://www.w3.org/2000/svg"
            >
              <title>Menu</title>
              <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" width={3} />
            </svg>
          </div>

        </div>
      </nav >
      {isOpened && (<div className=' md:hidden '>
        <ul className='rounded-lg transition-all bg-nesco-bg-blue shadow-md fixed right-0 top-20 list-none text-white z-10'>
          <li className=' p-4 px-8'><Link to="/" className='hover:text-blue-200'>FAQ</Link></li>
          <li className=' p-4 px-8'>
            <Menu>
              <Menu.Button>Competitions</Menu.Button>
              <Transition
                enter="transition ease-out duration-100"
                enterFrom="transform opacity-0 scale-95"
                enterTo="transform opacity-100 scale-100"
                leave="transition ease-in duration-75"
                leaveFrom="transform opacity-100 scale-100"
                leaveTo="transform opacity-0 scale-95"
                className='relative'
              >
                <Menu.Items as='ul'>
                  <Menu.Item as='li'>
                    {({ active }) => (
                      <Link to="/competitions/paper">
                        <a
                          className={`${active && "text-nesco-text-purple"
                            } rounded-md text-center p-2 text-sm`}
                        >
                          Paper Competition
                        </a>
                      </Link>
                    )}
                  </Menu.Item>
                  <Menu.Item as='li'>
                    {({ active }) => (
                      <Link to="/competitions/poster">
                        <a
                          className={`${active && "text-nesco-text-purple"
                            } rounded-md text-center p-2 text-sm`}
                        >
                          Poster Competition
                        </a>
                      </Link>
                    )}
                  </Menu.Item>
                  <Menu.Item as='li'>
                    {({ active }) => (
                      <Link to="/competitions/ei">
                        <a
                          className={`${active && "text-nesco-text-purple"
                            } rounded-md text-center p-2 text-sm`}
                        >
                          Electrical Innovation
                        </a>
                      </Link>
                    )}
                  </Menu.Item>
                </Menu.Items>
              </Transition>
            </Menu>
          </li>
          <li className=' p-4 px-8'><Link to="/" className='hover:text-blue-200'>Webinar</Link></li>
          <li className=' p-4 px-8'><Link to="/" className='hover:text-blue-200'>Gallery</Link></li>
          {!isAuth() ?
          <li className=' p-4 px-8'> <Link to="/login" className='hover:text-blue-200'>Sign In</Link></li> :null }
          {isAuth() ?
          <li className=' p-4 px-8'><Link to="/debug" className='hover:text-blue-200'>Debug</Link></li> : null}
        </ul>
      </div>)
      }
    </div >
  );
}