import React, { useState, useEffect } from 'react';
import authSvg from '../assets/update.svg';
import { ToastContainer, toast } from 'react-toastify';
import axios from 'axios';
import { updateUser, isAuth, getCookie, signout, updateUserData } from '../helpers/auth';
import { Link, Redirect } from 'react-router-dom';
const DashboardLomba= ({ history }) => {
  const [formData, setFormData] = useState({
    lomba_poster: '',
    link_submisi_poster :'',
    bukti_pembayaran_poster: '',
  });

  useEffect(() => {
    loadProfile();
  }, []);

  const loadProfile = () => {
    const token = getCookie('token');//mengambil token yang disimpan di dalam cookie
    axios
      .get(`${process.env.REACT_APP_API_URL}/userData/${isAuth()._id}`, {
        headers: { // masih bingung gunanya headers ? 
          Authorization: `Bearer ${token}`
        }
      })
      .then(res => {
        const {lomba_poster, link_submisi_poster, bukti_pembayaran_poster } = res.data;
        setFormData({ ...formData, lomba_poster, link_submisi_poster, bukti_pembayaran_poster  });
      })
      .catch(err => {
        toast.error(`Eror mengenai informasi anda ${err.response.statusText}`);
        if (err.response.status === 401) {
          signout(() => {
            history.push('/login');
          });
        }
      });
  };
  const {  lomba_poster, link_submisi_poster, bukti_pembayaran_poster  } = formData;

  
  //Disini akan berisikan berbagai macam informasi dari profile apakah editable atau tidak saya tidak tau 
  return (
    
          <div className='mt-12 flex flex-col items-center'>
            <h1 className='text-2xl xl:text-3xl font-extrabold'>
              Lomba Poster
            </h1>

            <form
              className='w-full flex-1 mt-8 text-indigo-500'
            >
              <div className='mx-auto max-w-xs relative '>
                  <span className = "font-medium ">
                     Status Pembayaran
                  </span>
                <input
                  className='w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5'
                  type='text'
                  placeholder='Status'
                  disabled
                  value={lomba_poster}
                />
              
                
              </div>
              <Link
                  to='/buktiposter'
                  className='mt-5 tracking-wide font-semibold bg-gray-500 text-gray-100 w-full py-4 rounded-lg hover:bg-gray-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none'
                >
                  <i className='fas fa-user-plus  w-6  -ml-2' />
                  <span className='ml-3'>Bukti Pembayaran</span>
                </Link> 
                <button
                onClick={()=> window.open(bukti_pembayaran_poster, "_blank") }
                className='mt-5 tracking-wide font-semibold bg-indigo-500 text-gray-100 w-full py-4 rounded-lg hover:bg-indigo-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none'
              >
                 <span className='ml-3'>Download Bukti Pembayaran</span>
              </button>
              
                <Link
                  to='/submisiposter'
                  className='mt-5 tracking-wide font-semibold bg-gray-500 text-gray-100 w-full py-4 rounded-lg hover:bg-gray-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none'
                >
                  <i className='fas fa-user-plus  w-6  -ml-2' />
                  <span className='ml-3'>Upload Submisi</span>
                </Link>
                <button
                onClick={()=> window.open(link_submisi_poster, "_blank") }
                className='mt-5 tracking-wide font-semibold bg-indigo-500 text-gray-100 w-full py-4 rounded-lg hover:bg-indigo-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none'
              >
                 <span className='ml-3'>Download Submisi Poster</span>
              </button>
              <div className='my-12 border-b text-center'>
                <div className='leading-none px-2 inline-block text-sm text-gray-600 tracking-wide font-medium bg-white transform translate-y-1/2'>
                  Go To Home
                </div>
              </div>
              <div className='flex flex-col items-center'>
                <a
                  className='w-full max-w-xs font-bold shadow-sm rounded-lg py-3
           bg-indigo-100 text-gray-800 flex items-center justify-center transition-all duration-300 ease-in-out focus:outline-none hover:shadow focus:shadow-sm focus:shadow-outline mt-5'
                  href='/'
                  target='_self'
                >
                  <i className='fas fa-sign-in-alt fa 1x w-6  -ml-2 text-indigo-500' />
                  <span className='ml-4'>Home</span>
                </a>
              </div>
               
            </form>
          
              
           

          </div>
        
  );
};

export default DashboardLomba;
