import React, { useEffect } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { signout } from './helpers/auth';
import { ToastContainer, toast } from 'react-toastify';
import { authenticate, isAuth, LocalUserData } from '../src/helpers/auth';
import HomeNescoVector from './assets/HomeNescoVector.svg';
import TeaserVideo from './assets/video/TeaserVideo.mp4';
import ProfilePhoto from './assets/PakSarjiyaAndFriends.png';
import DonatTransparan from './assets/DonatTransparan.svg';
import XTransparan from './assets/XTransparan.svg';
import Navbar from './components/Navbar';
import Footer from './components/Footer';
import timeline from './assets/garisTimeline.png';
import timelineText from './assets/TIMELINE.svg';
import bgCompetitions from './assets/bgCompetitions.svg';
import bgTimeline from './assets/bgTimeline.svg';
import posterComp from './assets/poster.png';
import paperComp from './assets/paper.png';
import elComp from './assets/el.png';


function Home({ history }) {
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])
  return (
    <div>

      <div className='bg-gray-100 text-gray-900 relative overflow-hidden -space-y-28'>
        <ToastContainer />
        <video className=' object-cover min-h-screen w-full' src={TeaserVideo} type="video/mp4" autoPlay loop muted>
        </video>

        <div className='w-full'>
          <div className=' relative flex justify-center bg-cover' style={{ backgroundImage: "url(" + HomeNescoVector + ")" }}>
            {/* <img className='w-full min-h-screen object-cover' src={HomeNescoVector} alt=''></img> */}
            <div className=' lg:grid lg:grid-cols-2 gap-2 bg-white/30 backdrop-blur-3xl h-3/5 w-3/4 rounded-3xl outline outline-2 outline-white/50 p-12 my-32 md:my-96'>
              <img src={ProfilePhoto} alt='' className=' w-full object-scale-down order-1 md:order-2'></img>
              <div className=' order-2 md:order-1 flex items-center flex-wrap '>
                <div>
                  <h1 className='text-3xl lg:text-5xl w-full text-center'>National Electrical Power System Competition</h1>
                  <p className=' py-8 text-justify w-full'>
                  National Electrical Power System Competition (NESCO) merupakan sebuah kompetisi tingkat nasional Badan Semi Otonom (BSO) Magatrika (Mahasiswa Ketenagalistrikan) yang berada di bawah arahan Keluarga Mahasiswa Teknik Elektro dan Teknologi Informasi Fakultas Teknik Universitas Gadjah Mada (KMTETI FT UGM) yang membahas mengenai permasalahan listrik tegangan tinggi yang bertujuan untuk meningkatkan perkembangan ketenagalistrikan khususnya di Indonesia.
                  </p>
                </div>
              </div>

            </div>
          </div>
        </div>
        <img src={DonatTransparan} alt='' className='absolute -left-24 top-[784px] w-2/3 max-w-[512px]'></img>
        <img src={XTransparan} alt='' className='absolute w-2/3 max-w-[512px] -right-28 top-[1800px]'></img>
      </div>

      <div className='w-full bg-gradient-to-r from-[#8333F3] to-[#45A9EB] h-screen' >
        <div className='relative grid gap-12 justify-center bg-center bg-cover rounded-t-[100px] h-screen' style={{backgroundImage:"url("+ bgTimeline+")"}}>
            <img src={timelineText} alt='' className='m-auto block pt-24 '></img>
            <img src={timeline} alt='' className='pb-48 items-center w-screen'></img> 
        </div>
      </div>
      
      <div className='w-full h-screen bg-gradient-to-br from-[#3DB9EA] to-[#6454F0]'>
        <div className='grid relative justify-center bg-cover gap-24' >
          <h1 className='text-white text-3xl lg:text-5xl w-full text-center mt-24'>Competitions</h1>
          <div className='md:flex gap-36'>
              <img src={paperComp} alt='' className=''></img>
              <img src={posterComp} alt='' className=''></img>
              <img src={elComp} alt='' className=''></img>
        
          </div>
        </div>
        {/* <img src={paperComp} alt='' className='absolute pt-48 '></img> */}
      </div>

      <Navbar></Navbar>
      <Footer></Footer>
    </div>
  );
}

export default Home;