import React, { useState } from 'react';
import authSvg from '../assets/forget.svg';
import { ToastContainer, toast } from 'react-toastify';
import axios from 'axios';
import { Link, Redirect } from 'react-router-dom';
import loginBackground from '../assets/LoginBackground.png';
import logoNesco from '../assets/LogoNesco.png';

const ForgetPassword = () => {
  const [formData, setFormData] = useState({
    email: '',
    textChange: 'Submit'
  });
  const { email } = formData;
  const handleChange = text => e => {
    setFormData({ ...formData, [text]: e.target.value });
  };
  const handleSubmit = e => {
    e.preventDefault();
    if (email) {
      setFormData({ ...formData, textChange: 'Submitting' });
      axios
        .put(`${process.env.REACT_APP_API_URL}/forgotpassword`, {
          email
        })
        .then(res => {

          setFormData({
            ...formData,
            email: '',
          });
          toast.success(`Link reset password terkirim ke email anda. Pesan dapat berada di folder spam`);

        })
        .catch(err => {
          console.log(err.response)
          toast.error(err.response.data.error);
        });
    } else {
      toast.error('Isikan semua informasi');
    }
  };
  return (
    <header className='flex w-full' style={{ backgroundImage: "url(" + loginBackground + ")", backgroundSize: 'cover' }}>
      <ToastContainer />
      <Link to="/">
      <img src={logoNesco} className='h-32'></img>
      </Link>
      <Link to="/"><h1 className=' text-white my-8 text-4xl hover:text-nesco-text-purple transition-colors'>NESCO 2022</h1></Link>
      <form onSubmit={handleSubmit} className=' bg-nesco-cyan/30 backdrop-blur-md flex-1 h-1/3 max-h-96 w-2/3 max-w-xl rounded-3xl outline outline-2 outline-nesco-blue/80 p-6 place-items-end'>
        <p className=' text-white text-xl'>Masukkan email anda untuk mengatur ulang kata sandi</p>
        <div className=' h-5'></div>
        <input className=' bg-transparent w-full under focus:outline-none placeholder:text-white text-white' placeholder="Email" onChange={handleChange('email')} value={email} ></input>
        <hr></hr>
        <div className=' my-8 mx-auto text-center'>
          <button type='submit' className=' just my-4 text-center mx-auto px-10 py-2 rounded-full text-white' style={{ background: "linear-gradient(109.93deg, #FFA62E 10.81%, rgba(255, 166, 46, 0.7) 60.13%, rgba(255, 166, 46, 0.5) 92.27%)" }}>
            <h1>SUBMIT</h1>
          </button>
        </div>
      </form>
    </header>
    
  );
};

export default ForgetPassword;
