import React, { useState, useEffect, useContext } from 'react';
import authSvg from '../assets/login.svg';
import { ToastContainer, toast } from 'react-toastify';
import axios from 'axios';
import { authenticate, isAuth } from '../helpers/auth';
import { Link, Redirect, useHistory } from 'react-router-dom';
import 'react-toastify/dist/ReactToastify.css';



import { signout } from '../helpers/auth';

import { getCookie } from '../helpers/auth';
import { faHome, faPeopleArrows, faTrophy, faUser, faUsers } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';




const DashboardTemplate = (props) => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    link_profil: ''
  });
  const history = useHistory();
  useEffect(() => {
    loadProfile();
  }, []);

  const loadProfile = () => {

    const token = getCookie('token');//mengambil token yang disimpan di dalam cookie
    axios
      .get(`${process.env.REACT_APP_API_URL}/user/${isAuth()._id}`, {
        headers: { // masih bingung gunanya headers ? 
          Authorization: `Bearer ${token}`
        }
      })
      .then(res => {
        const { name, email, link_profil } = res.data;
        setFormData({ ...formData, name, email, link_profil });
      })
      .catch(err => {
        toast.error(`Error To Your Information ${err.response.statusText}`);
        if (err.response.status === 401) {
          signout(() => {
            history.push('/login');
          });
        }
      });
  };
  const { name, email, link_profil } = formData;
  const History = useHistory();



  return (
    <div className='text-gray-900 flex justify-center' style={{ background: "linear-gradient(109.18deg, #9E00F6 -28.72%, #5092F3 111.79%)" }}>
      <ToastContainer />
      <div className='w-full flex h-screen'>

        {/*Sidebar*/}
        <div className=' h-full w-1/4 flex justify-center'>
          <div className=' flex flex-col justify-start p-12 h-full lg:h-[90%] w-4/5 rounded-b-[60px] bg-gradient-to-br from-[#5896F0] to-nesco-cyan items-center'>
            <div className='w-36 h-36 rounded-full my-8 bg-slate-300'>
              <img className='' src={link_profil}>
              </img>
            </div>
            <h1 className=' text-lg lg:text-2xl text-center text-white'>{name}</h1>
            <p className='text-white'>{email} </p>
            <button onClick={() => {
              signout(() => {
                History.push('/');
                toast.success('Berhasil keluar');
              });
            }} className=' my-8 py-2 px-6 shadow-md rounded-[50px] shadow-nesco-orange/25 hover:bg-white' style={{ background: "linear-gradient(103.19deg, #FEADD5 -1.34%, #FFA62E 109.01%)" }}>
              <h1 className=' text-nesco-text-purple text-lg'>Sign Out</h1>
            </button>
            <ul className=' w-full'>
              <SidebarElement icon={faHome} link="/beranda" title="Beranda"/>
              <SidebarElement icon={faUser} link="/profil" title="Profil"/>
              <SidebarElement icon={faUsers} link="/datadiri" title="Profil Tim"/>
              <SidebarElement icon={faTrophy} link="/lombaawal" title="Kompetisi"/>
              <SidebarElement icon={faUsers} link="/authwebinar" title="Webinar"/>
           
            </ul>
          </div>
        </div>


        <div className='w-3/4 px-16 h-full'>
          <h1 className='text-5xl py-8 text-transparent bg-clip-text bg-gradient-to-r from-nesco-light-pink to-nesco-cyan'>DASHBOARD BERANDA</h1>
          <div className=' bg-white shadow sm:rounded-lg flex justify-center lg:h-4/5 overflow-y-scroll '>
            {/* Content*/}
            {props.content}
            {/*End of Content*/}
          </div>
        </div>
      </div>

    </div>
  );
}

const SidebarElement = (props) => {
  return (
    <li className=' my-4 flex w-full justify-start text-center align-middle items-center'>
      <div className=' w-10 mx-3'><FontAwesomeIcon className='text-white' size='lg' icon={props.icon} /></div>
      <Link to={props.link} className=' mx-2 text-white text-lg hover:text-nesco-text-purple transition-colors '>{props.title}</Link>
    </li>
  )
}

export default DashboardTemplate;