const [newUser4, setNewUser4] = useState(
  {   photo4: '',
  }
  
);
const [newUser5, setNewUser5] = useState(
  {    photo5: '',
  }

);
  
  useEffect(() => {
    loadProfile();
  }, []);

  const loadProfile = () => {//Dikode ini juga dapat ditambahkan link untuk ktm
  
    const token = getCookie('token');//mengambil token yang disimpan di dalam cookie
    axios
      .get(`${process.env.REACT_APP_API_URL}/userData/${isAuth()._id}`, {
        headers: { // masih bingung gunanya headers ? ///
          Authorization: `Bearer ${token}`
        }
      })
      .then(res => {
        const { institusi, nama_tim, email, nama_ketua, nama_anggota_1, nama_anggota_2,
          nomor_telepon_ketua,
          nomor_telepon_cadangan,link_ktm_anggota_1, link_ktm_anggota_2,link_ktm_ketua,
          link_foto_ketua, link_foto_anggota_1, link_foto_anggota_2 } = res.data;
        setFormData({ ...formData, institusi, nama_tim, email, nama_ketua, nama_anggota_1, nama_anggota_2,link_ktm_anggota_1, link_ktm_anggota_2,link_ktm_ketua,
          nomor_telepon_ketua,
          nomor_telepon_cadangan, link_foto_ketua, link_foto_anggota_1, link_foto_anggota_2  });
      })
      .catch(err => {
        toast.error(`Error untuk informasi anda ${err.response.statusText}`);
        if (err.response.status === 401) {
          signout(() => {
            history.push('/login');
          });
        }
      });

      
  };


  const {  institusi, nama_tim,email, link_ktm_ketua,
    nama_ketua, nama_anggota_1, nama_anggota_2,
    nomor_telepon_ketua,
    nomor_telepon_cadangan,link_ktm_anggota_1, link_ktm_anggota_2, link_foto_ketua, link_foto_anggota_1, link_foto_anggota_2,textChange } = formData;

  const handleChange = text => e => {
    setFormData({ ...formData, [text]: e.target.value });
  };

const handleSubmits = (e) => {
  toast.info("Proses upload membutuhkan waktu. Tunggu hingga notifikasi terunggah muncul ")
    e.preventDefault();
    const image = newUser.photo;
    new Compressor(image, 
      {convertTypes : 'image/png',convertSize : 500000, 
      success: (compressedResult) => {
        const formData = new FormData();
        formData.append('file', compressedResult , compressedResult.name  );
        axios.post(`${process.env.REACT_APP_API_URL}/upload`, formData )
         .then(res => {
          let sementara = LocalUserData()
          console.log(sementara)
          updateUserImageData(res, () => {
              toast.success('Foto KTM ketua berhasil terunggah. Jangan lupa simpan perubahan');
               
            });
         })
         .catch(err => {
            console.log(err);
         });
      },
    });
    
}

