const handleDeleteF1 = (type) => {
    

    let URL = ''
    const {link_ktm_ketua, link_ktm_anggota_1, link_ktm_anggota_2, link_foto_ketua, link_foto_anggota_1, link_foto_anggota_2 } = LocalUserData();
    console.log(link_foto_anggota_1);
    if (type === "fotoA1"){
      URL = link_foto_anggota_1
    }
    else if(type === "fotoA2"){
      URL = link_foto_anggota_2
    }
    else if(type === "fotoKetua"){
      URL = link_foto_ketua
    }
    else if(type === "KTMKetua"){
      URL = link_ktm_ketua
    }
    else if(type === "KTMA1"){
      URL = link_ktm_anggota_1
    }
    else if(type === "KTMA2"){
      URL = link_ktm_anggota_2
    }
    
    axios.post(`${process.env.REACT_APP_API_URL}/deleteImage`, { 
      URL
      })
         .then(res => {
          let sementara = LocalUserData()
          console.log(sementara)
           console.log("TEST")
            //ingin disimpan di localdata
             if (type === "fotoA1"){
              updateUserImageA1(res, () => {
              toast.success('Gambar Terhapus');
            });
            }
            else if(type === "fotoA2"){
              updateUserImageA2(res, () => {
              toast.success('Gambar Terhapus');
            });
            }
            else if(type === "fotoKetua"){
              updateUserImageKetua(res, () => {
              toast.success('Gambar Terhapus');
            });
            }
            else if(type === "KTMKetua"){
              updateUserImageData(res, () => {
              toast.success('Gambar Terhapus');
            });
            }
            else if(type === "KTMA1"){
              updateUserImageKTM1(res, () => {
              toast.success('Gambar Terhapus');
            });
            }else if(type === "KTMA2"){
              updateUserImageKTM2(res, () => {
              toast.success('Gambar Terhapus');
            });
            }
    
            
           
            
         })
         .catch(err => {
            console.log(err);
         });
}

