const handleSubmit = e => {
  const {link_ktm_ketua,
    link_ktm_anggota_1, link_ktm_anggota_2,link_foto_ketua, link_foto_anggota_1, link_foto_anggota_2 } = LocalUserData();

  let sementara = LocalUserData();
  console.log("TESTTT")
   console.log(sementara)
  const token = getCookie('token');
  console.log(token);
  console.log(nomor_telepon_ketua);
  console.log(link_ktm_anggota_1);
  e.preventDefault();

  setFormData({ ...formData, textChange: 'Submitting' });
  axios
    .put(
      `${process.env.REACT_APP_API_URL}/userData/update`,
      {

        institusi, nama_tim,email, link_ktm_ketua,
        nama_ketua, nama_anggota_1, nama_anggota_2,
        nomor_telepon_ketua,
        nomor_telepon_cadangan,link_ktm_anggota_1, link_ktm_anggota_2,
        link_foto_ketua, link_foto_anggota_1, link_foto_anggota_2
      },
      {
        headers: {
          Authorization: `Bearer ${token}`//apakah berhubungan dengan JWT ? yea ternyata berhubungan pada JWT_SECRET require Sign-in
        }
      }
    )
    .then(res => {
      updateUserData(res, () => {
        toast.success('Data Tim berhasil diupdate');
        setFormData({ ...formData, textChange: 'Update' });
      });
      loadProfile();
    })
    .catch(err => {
      setFormData({
        ...formData,

      });
      toast.error(err.response.data.error);
      console.log(err.response);


    });
};


