import React, { useState, useEffect } from 'react';
import authSvg from '../assets/welcome.svg';
import { ToastContainer, toast } from 'react-toastify';
import axios from 'axios';
import jwt_decode from 'jwt-decode';
import { TerdaftarWebinar,getCookie, updateProfileWebinar,TerupdateDiri} from '../helpers/auth';
import { Link, Redirect } from 'react-router-dom';
import 'react-toastify/dist/ReactToastify.css';
import { useParams, useHistory  } from "react-router-dom";



const Webinar = () => {
  const history = useHistory();
  useEffect(() => {
    const Terdaftar1 = TerupdateDiri();
  if(Terdaftar1 != false){
    history.push("/authwebinar")
  }
   const Terdaftar = TerdaftarWebinar();
   if(Terdaftar == true){
     history.push("/terdaftarwebinar")
   }
}, [])

  const handleSubmit = e => {
    e.preventDefault();
    //send the token the activation API backend
    const token = getCookie('token');//mengambil token yang disimpan di dalam cookie
    ;
    axios
      .post(`${process.env.REACT_APP_API_URL}/user/updateWebinar`, {
      },
      {
        headers: {
          Authorization: `Bearer ${token}`//apakah berhubungan dengan JWT ? yea ternyata berhubungan pada JWT_SECRET require Sign-in
        }
      })
      .then(res => {
        updateProfileWebinar(res, () => {
         
        });
        history.push("/terdaftarwebinar")// langsung go to webinar terdaftar
        toast.success("Anda berhasil mendaftarkan webinar");
        
        res.json(200);
      })
      .catch(err => {
        
        toast.error(err.response.data.errors);
      });
  };

  return (
    
          <div className='mt-12 flex flex-col items-center'>
            <h1 className='text-2xl xl:text-3xl font-extrabold'>
              Anda belum mendaftarkan webinar
            </h1>

            <form
              className='w-full flex-1 mt-8 text-indigo-500'
              onSubmit={handleSubmit}
            >
              
              <div className='mx-auto max-w-xs relative '>
                <button
                  type='submit'
                  className='mt-5 tracking-wide font-semibold bg-indigo-500 text-gray-100 w-full py-4 rounded-lg hover:bg-indigo-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none'
                >
                  <i className='fas fa-user-plus fa 1x w-6  -ml-2' />
                  <span className='ml-3'>Daftar </span>
                </button>
              </div>
              <div className='my-12 border-b text-center'>
                <div className='leading-none px-2 inline-block text-sm text-gray-600 tracking-wide font-medium bg-white transform translate-y-1/2'>
                  Or sign up again
                </div>
              </div>
              <div className='flex flex-col items-center'>
                <a
                  className='w-full max-w-xs font-bold shadow-sm rounded-lg py-3
           bg-indigo-100 text-gray-800 flex items-center justify-center transition-all duration-300 ease-in-out focus:outline-none hover:shadow focus:shadow-sm focus:shadow-outline mt-5'
                  href='/register'
                  target='_self'
                >
                  <i className='fas fa-sign-in-alt fa 1x w-6  -ml-2 text-indigo-500' />
                  <span className='ml-4'>Sign Up</span>
                </a>
              </div>
            </form>
          </div>
        
  );
};

export default Webinar;
