import React, { useState } from 'react';
import authSvg from '../assets/login.svg';
import { ToastContainer, toast } from 'react-toastify';
import axios from 'axios';
import { authenticate, isAuth } from '../helpers/auth';
import { Link, Redirect } from 'react-router-dom';
import 'react-toastify/dist/ReactToastify.css';

import loginBackground from '../assets/LoginBackground.png';
import logoNesco from '../assets/LogoNesco.png';

const Login = ({ history }) => {
  const [formData, setFormData] = useState({
    email: '',
    password1: '',
    textChange: 'Sign In'
  });
  const { email, password1, textChange } = formData;
  const handleChange = text => e => {
    setFormData({ ...formData, [text]: e.target.value });
  };


  const handleSubmit = e => {
    console.log(process.env.REACT_APP_API_URL);
    e.preventDefault();
    if (email && password1) {
      setFormData({ ...formData, textChange: 'Submitting' });
      axios
        .post(`${process.env.REACT_APP_API_URL}/login`, {
          email,
          password: password1
        })
        .then(res => {// Authenticate MEMBUAT SET COOKIE TOKEN (JWT SECRET)
          authenticate(res, () => {
            console.log(isAuth())

            setFormData({
              ...formData,
              email: '',
              password1: '',
              textChange: 'Submitted'
            });

            if (isAuth() && isAuth().role === 'admin') { history.push('/admin'); toast.success(`Selamat datang ${res.data.user.name}!`);}
            else { history.push('/beranda'); toast.success(`Selamat datang ${res.data.user.name}!`);};

          });
        })
        .catch(err => {
          setFormData({
            ...formData,
            email: '',
            password1: '',
            textChange: 'Sign In'
          });
          console.log(err.response);
          toast.error(err.response.data.errors);
        });
    } else {
      toast.error('Isikan keseluruhan informasi Anda');
    }
  };
  return (
    <header className='flex w-full' style={{ backgroundImage: "url(" + loginBackground + ")", backgroundSize: 'cover' }}>
      {isAuth() ? <Redirect to='/dashboard' /> : null} {/* JIKA ADA COOKIE MAKA REDIRECT KE HOME */}
      <ToastContainer />
      <Link to="/">
        <img alt='' src={logoNesco} className='h-32'></img>
      </Link>
      <Link to="/"><h1 className=' text-white my-8 text-4xl hover:text-nesco-text-purple transition-colors'>NESCO 2022</h1></Link>

      <form onSubmit={handleSubmit} className=' bg-nesco-cyan/30 backdrop-blur-md flex-1 h-1/3 max-h-96 w-2/3 max-w-xl rounded-3xl outline outline-2 outline-nesco-blue/80 p-6'>
        <input className=' text-white bg-transparent w-full under focus:outline-none placeholder:text-white' placeholder="Email" onChange={handleChange('email')} value={email} ></input>
        <hr></hr>
        <div className=' h-5'></div>
        <input type={'password'} className=' text-white placeholder:text-white bg-transparent w-full under focus:outline-none' placeholder="Password" onChange={handleChange('password1')} value={password1}></input>
        <hr></hr>
        <div className=' h-7'></div>
        <Link className=' text-xl text-white hover:text-nesco-text-purple transition-colors' to='/users/password/forget'>Lupa Kata Sandi</Link>
        <br></br>
        <div className=' mx-auto text-center'>
          <button type='submit' className=' my-4 text-center mx-auto px-10 py-2 rounded-full text-white ' style={{ background: "linear-gradient(109.93deg, #FFA62E 10.81%, rgba(255, 166, 46, 0.7) 60.13%, rgba(255, 166, 46, 0.5) 92.27%)" }}>
            <h1>LOGIN</h1>
          </button>
          <div className=' block md:flex justify-center'>
            <p className=' text-lg text-white'>Belum punya akun?</p>
            <div className=' w-2'></div>
            <Link className=' text-lg text-white hover:text-nesco-text-purple transition-colors' to='/register'>Daftar</Link>
          </div>
        </div>


      </form>
    </header>

  );
};

export default Login;
